.colors-palette {
  border-top: 1px solid rgba(51, 51, 51, 0.16470588235294117);
  padding-top: 8px;
  margin-top: 8px;
  > * {
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 1.5px;
    border-radius: 50%;
    border: 1px solid #00000026;
    cursor: pointer;
  }
}
