.task-box-preview {
  font-size: .75rem;
  justify-items: center;
  min-width: 680px;
  -webkit-flex-grow: 1;
  flex-grow: 1;
  display: grid;
  /* height: 36px; */
  grid-template-columns: 3px minmax(200px,1fr) 50px 120px 120px 120px 120px 3px;
  grid-template-rows: 40px;
  margin-bottom: 2.8px;
  -webkit-align-items: center;
  align-items: center;
  grid-gap: 1px 1.5px;
  position: relative;
  box-shadow: 0 0 0 0.3px #e3e3e3;
  background-color: #f3f3f3;
  & > * {
    &:first-child {
      width: 5px;
      height: 100%;
    }
    &:nth-child(2) {
      justify-self: start;
      padding: 0 1.5px;
      margin: 0 3px;
    }
    &:nth-child(5) {
      justify-self: center;
      padding: 0 0.5px;
      margin: 0 3px;
    }
    &:last-child {
      width: 5px;
      height: 100%;
    }
  }
  .task-title-container {
    .delete-icon{
      cursor: pointer;
    }
    width: 100%;
    font-size: rem(12px);
    align-items: center;
    display: flex;
  }
  .editable-txt-prev {
    width: 100%;
    padding: 5px 5px 5px 4px;
    border: 1px dashed transparent;
    &:hover {
      border: 1px dashed #9ea2b3;
    }
  }
  &:hover {
    background-color: #cce5ff;
  }
  .editable-form {
    width: 100%;
    .editable-input {
      width: 100%;
      padding: 5px 5px 5px 4px;
      font-size: 12px;
      background-color: transparent;
      border: 1px dashed $black;
      background-color: #cce5ff;
    }
  }
}
.chat-icon-container {
  align-self: end;
  padding-bottom: 3px;
  .chat-icon {
    color: #b6b7bd;
    cursor: pointer;
    font-size: 22px;
    margin-bottom: 2px;
    &:hover {
      color: #0073ea;
    }
  }
}
.chat-msgs-count {
  background-color: #0073ea;
  color: white;
  line-height: 10px;
  font-size: 10px;
  border-radius: 10px;
  text-align: center;
  padding: 2px 4px;
  position: relative;
  left: 7px;
  margin: 0px;
  top: 23px;
}

.task-box-container {
  .delete-icon {
    opacity: 0;
    color: rgb(172, 172, 172);
    font-size: rem(16px);
    transition: opacity 0.3s;
    margin-left: 5px;
  }
  .drag-icon {
    opacity: 0.25;
    // padding-right: 3.5px;
    &:hover {
      opacity: 0.5;
    }
    & > * {
      font-size: rem(20px);
    }
  }

  &:hover {
    .delete-icon {
      opacity: 1;
      &:hover {
        color: $black;
      }
    }
  }
  .member-avatars-main {
    cursor: pointer;
    width: 60px;
    .avatar-container {
      width: 24px;
      height: 24px;
      background-color: #636363;
      border: solid 0.5px;
      font-size: 12px;
      font-weight: 500;
    }
  }
}

.alert-contaier {
  position: absolute;
  top: 10px;
  right: 40%;
  z-index: 20;
  .success-alert {
    display: flex;
    align-items: center;
    height: 40px;
    background-color: #0f9147;
    padding: 0 30px;
    color: #fefffe;
    .MuiAlert-icon {
      color: #fefffe;
    }
  }
}
